  },
  components: { ProductCard },
  beforeRouteLeave(to, from, next) {
    // Stop the execution of WriteLetter and RemoveLetter methods
    clearTimeout(this.writeLetterTimeout);
    clearTimeout(this.removeLetterTimeout);
    next();
  },

  methods: {
    RemoveLetter() {
      this.removeLetterTimeout = setTimeout(() => {
        this.myletter = this.myletter.slice(0, this.myletter.length - 2);
        this.$refs.text.innerHTML = this.myletter + "_";
        if (this.i > 0) {
          this.i -= 1;
          this.RemoveLetter();
        } else {
          this.writeLetterTimeout = setTimeout(() => {
            this.WriteLetter();
          }, this.animation_speed);
        }
      }, this.writing_speed);
    },
    WriteLetter() {
      this.writeLetterTimeout = setTimeout(() => {
        this.myletter += this.letters[this.index][this.i];
        this.$refs.text.innerHTML = this.myletter + "_";
        if (this.i < this.letters[this.index].length - 1) {
          this.i += 1;
          this.WriteLetter();
        } else {
          if (this.index < Object.keys(this.letters).length - 1) {
            this.index += 1;
          } else {
            this.index = 0;
          }
          this.removeLetterTimeout = setTimeout(() => {
            this.RemoveLetter();
          }, this.animation_speed);
        }
      }, this.writing_speed);
    },
  },

  mounted() {
    const getNav = document.getElementById("nav-filter");
    getNav.classList.remove("animate"); // Remove animation class when hiding
    setTimeout(() => {
      getNav.style.display = "none";
    }, 500); // Wait for the animation to complete before hiding
    if (this.$refs.text) {
      this.WriteLetter();
    }
  },
};
